.App {
    text-align: center;
    overflow: hidden;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

.arrowButton.correct {
    animation: correct 500ms 1;
}
.arrowButton.incorrect {
    animation: incorrect 500ms 1;
}

@keyframes correct {
    from {
        color: green;
    }
    to {
        color: white;
    }
}
@keyframes incorrect {
    from {
        color: red;
    }
    to {
        color: white;
    }
}

